import L from 'leaflet';
import { memo, useMemo, useRef, useEffect } from 'react';
import { renderToString } from 'react-dom/server';
import { createRoot, Root } from 'react-dom/client';
import dynamic from 'next/dynamic';
import { useSelector } from 'react-redux';
import { Marker as LeafletMarker } from 'react-leaflet';
import { I_MapConfig, I_Map } from '@containers/Home/types';
import { selectListScene } from '@containers/Home/selectors';
import { MarkerWrapper } from './styled';

const Radar = dynamic(() => import('./Radar'));

interface MarkerProps {
  northRadar: number;
  isActive: boolean;
  isTurnOnRadar: boolean;
  marker: I_Map['markers'][number];
  configMarker?: I_MapConfig['marker'];
  configRadar: I_MapConfig['radar'];
}

interface Props extends MarkerProps {
  lat: number;
  lng: number;
  onClick?: () => void;
}

export const ContentMarker = memo(
  ({
    marker,
    isTurnOnRadar,
    configMarker,
    configRadar,
    northRadar,
    isActive,
  }: MarkerProps) => (
    <MarkerWrapper id={marker.id} className={isActive ? 'active' : ''}>
      <img
        src={
          (isActive ? configMarker?.srcWhenActive : configMarker?.src) ||
          configMarker?.src ||
          'data:image/png;base64,'
        }
        style={{
          width: configMarker?.width ? `${configMarker?.width}px` : '20px',
        }}
        alt="dot"
      />
      {isActive && isTurnOnRadar && (
        <Radar
          width={configRadar?.width || 40}
          bgColor={configRadar?.bgColor || ''}
          north={northRadar || 0}
          borderColor={configRadar?.borderColor || ''}
          borderWidth={configRadar?.borderWidth || 2}
        />
      )}
    </MarkerWrapper>
  ),
);

const Marker = memo(({ marker, lat, lng, onClick, ...props }: Props) => {
  const rootRef = useRef<Root | null>(null);

  const listScene = useSelector(selectListScene);

  const sceneSelected = useMemo(
    () =>
      listScene.find(item => item.id === marker.target_scene_id) || undefined,
    [listScene, marker.target_scene_id],
  );

  const customIcon = useMemo(() => {
    return L.divIcon({
      html: renderToString(<div id={`icon_${marker.id}`} />),
      className: 'custom-leaflet-marker',
      iconSize: [0, 0],
    });
  }, [marker.id]);

  useEffect(() => {
    const markerElement = document.getElementById(`icon_${marker.id}`);
    if (markerElement && !rootRef.current)
      rootRef.current = createRoot(markerElement);

    if (rootRef.current)
      rootRef.current.render(
        <abbr title={sceneSelected?.title || ''}>
          <ContentMarker marker={marker} {...props} />
        </abbr>,
      );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [marker, props]);

  return (
    <LeafletMarker
      position={[lat, lng]}
      icon={customIcon}
      eventHandlers={{
        click: onClick,
      }}
    />
  );
});

export default Marker;
